import React from 'react';
import styled from 'styled-components';
import Link from '@src/components/common/Link';
import LinkList from '../common/LinkList';
import theme from '@src/style/theme';

const Nav = styled.nav`
width: 90%;
margin-left: auto;
margin-right: auto;
margin-top: 1rem;


h1 {
  font-size: 1.4rem;
  padding-bottom: 0.4rem;
  border-bottom: 2px solid ${theme.grey};
}
ul {
  display: flex;
  flex-direction: column;
  
  a {
    width: 100%;
    display: inline-flex;
    align-items: center;
    
    img {
      justify-self: flex-start;
      width: 3rem;
    }
  }
}

`;

const Category = () => {
  return (
    <Nav>
      <LinkList title="포지션별">
        <Link href="/list/[category]" as="/list/top">
          <img src="/static/images/Top.png" />
          탑
        </Link>
        <Link href="/list/[category]" as="/list/jungle">
        <img src="/static/images/Jungle.png" />
          정글
        </Link>
        <Link href="/list/[category]" as="/list/mid">
        <img src="/static/images/Mid.png" />
          미드
        </Link>
        <Link href="/list/[category]" as="/list/ad">
        <img src="/static/images/Bot.png" />
          원딜
        </Link>
        <Link href="/list/[category]" as="/list/supporter">
        <img src="/static/images/Support.png" />
          서폿
        </Link>
      </LinkList>
    </Nav>
  );
};

export default Category;
